import React from "react";

export default class OftenUseCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = { sites: [] };
  }

  componentDidMount() {
    let request = new XMLHttpRequest();
    request.open("get", "./oftenuse.json");
    request.responseType = "text";
    request.send();
    request.onload = function () {
      const root = JSON.parse(request.response);
      const sites = root["sites"];
      let sitesInRow = [];
      let sitesInTable = [];
      for (let i = 0; i < sites.length; ++i) {
        if (i % 6 === 0) {
          sitesInRow = [];
          sitesInTable.push(sitesInRow);
        }
        sitesInRow.push(sites[i]);
      }
      this.setState({ sites: sitesInTable });
    }.bind(this);
  }

  render() {
    return (
      <div className="card container grid-xl">
        <div className="card-header h2 text-center">常用网站</div>
        <div className="card-body">
          {this.state.sites.map((row, rowIndex) => (
            <div key={rowIndex}>
              <div className="btn-group btn-group-block">
                {row.map((item, index) => (
                  <button
                    key={index}
                    className="btn"
                    onClick={() => window.open(item.url, "_blank")}
                  >
                    {item.title}
                  </button>
                ))}
              </div>
              <p></p>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
