import React from "react";

export default class FavoriteCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = { sites: [] };
  }

  componentDidMount() {
    let request = new XMLHttpRequest();
    request.open("get", "./favorite.json");
    request.responseType = "text";
    request.send();
    request.onload = function () {
      const root = JSON.parse(request.response);
      const categories = root["categories"];
      this.setState({ sites: categories });
    }.bind(this);
  }

  render() {
    return (
      <div className="card container grid-xl">
        <div className="card-header h2 text-center">收藏夹</div>
        <div className="card-body columns col-gapless">
          {this.state.sites.map((category, cateIndex) => (
            <div key={cateIndex} className="column col-6 form-group">
              <span className="label label-primary">{category.name}</span>
              <select
                className="form-select"
                onChange={(e) => {
                  window.open(e.target.value, "_blank");
                }}
              >
                {category.sites.map((site, siteIndex) => (
                  <option key={siteIndex} value={site.url}>
                    {site.title}
                  </option>
                ))}
              </select>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
