import React from "react";

export default class SearchCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = { text: "" };

    this.handleChange = this.handleChange.bind(this);
    this.handleGoogle = this.handleGoogle.bind(this);
  }

  handleChange(e) {
    this.setState({ text: e.target.value });
  }

  handleGoogle() {
    window.open("https://www.google.com/search?q=" + this.state.text, "_blank");
  }

  render() {
    return (
      <div className="card container grid-xl">
        <div className="card-header">
          <div className="card-title h5">Search Something...</div>
        </div>
        <div className="card-body">
          <input
            className="form-input"
            type="text"
            onChange={this.handleChange}
          ></input>
        </div>
        <div className="card-footer">
          <div className="btn-group btn-group-block">
            <button className="btn" onClick={this.handleGoogle}>
              Google
            </button>
            <button className="btn">Bing</button>
            <button className="btn">Baidu</button>
          </div>
        </div>
      </div>
    );
  }
}
